import React, { Component } from "react";
import Joi from "joi-browser";
import Input from "./input";
class Form extends Component {
  state = { datas: {}, errors: {} };

  validate = () => {
    const options = { abortEarly: false };
    const { error } = Joi.validate(this.state.datas, this.schema, options);
    if (!error) return null;

    const errors = {};
    for (let item of error.details) {
      errors[item.path[0]] = item.message;
    }

    return errors;
  };
  
  validateProperty = ({ name, value }) => {
    const obj = { [name]: value };
    const schema = { [name]: this.schema[name] };
    const { error } = Joi.validate(obj, schema);
    return error ? error.details[0].message : null;
  };

  handleChange = ({ currentTarget: input }) => {
    const errors = { ...this.state.errors };
    const errorMessage = this.validateProperty(input);
    errorMessage !== ""
      ? (errors[input.name] = errorMessage)
      : delete errors[input.name];

    const datas = { ...this.state.datas };
    datas[input.name] = input.value;

    this.setState({ datas, errors });
  };
  handleSubmit = (e) => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) return;

    this.doSubmit();

    // console.log("handle submit-> datas:", datas);
    // this.setState({ datas });
  };
  renderInput(name, label, type = "text") {
    const { datas, errors } = this.state;
    return (
      <Input
        name={name}
        type={type}
        label={label}
        value={datas[name]}
        error={errors[name]}
        onChange={this.handleChange}
      />
    );
  }

  renderButton(label, type, data) {
    return (
      <button
        id="submitBtn"
        type={type}
        className="btn btn-success contactform-input submit-btn"
        disabled={this.validate()}
      >
        {label}
      </button>
    );
  }
}

export default Form;
